import React from 'react';
import Sidebar from '../components/Sidebar';
import { CopyToClipboard } from 'react-copy-to-clipboard';

const Contacts = () => {
    return (
        <div className="contacts">
            <Sidebar />
            <div className="contactContent">
                <div className="header"></div>
                <div className="contactBox">
                    <h1>Contactez-moi</h1>
                    <ul>
                        <li>
                            <i className="fas fa-map-marker-alt"></i>
                            <span>Marles Les Mines</span>
                        </li>

                        <li>
                            <i className="fas fa-mobile-alt"></i>
                            <CopyToClipboard text="06 15 11 33 32">
                                <span className="clickInput" onClick={() => { alert('Numéro de téléphone copié !');}}>06 15 11 33 32</span>
                            </CopyToClipboard>
                        </li>

                        <li>
                            <i className="far fa-envelope"></i>
                            <CopyToClipboard text="isabelle.guilherme@csenen.com">
                                <span className="clickInput" onClick={() => { alert('Adresse électronique copiée !');}}>isabelle.guilherme@csenen.com</span>
                            </CopyToClipboard>
                        </li>
                    </ul>
                </div>
                
                <div className="socialNetwork">
                    <ul>
                        <a href="https://www.kakumei.fr" target="_blank" rel="noopener noreferrer">
                            <h4>LinkedIn</h4>
                            <i className="fab fa-linkedin"></i>
                        </a>   
                        <a href="https://www.kakumei.fr" target="_blank" rel="noopener noreferrer">
                            <h4>Github</h4>
                            <i className="fab fa-github"></i>
                        </a>
                        <a href="https://www.kakumei.fr" target="_blank" rel="noopener noreferrer">
                            <h4>Twitter</h4>
                            <i className="fab fa-twitter"></i>
                        </a>
                        <a href="https://www.kakumei.fr" target="_blank" rel="noopener noreferrer">
                            <h4>CodePen</h4>
                            <i className="fab fa-codepen"></i>
                        </a>
                    </ul>
                </div>
            </div>
        </div>
    );
};

export default Contacts;