import React from 'react';
import { NavLink } from 'react-router-dom';

function Sidebar() {
    return (
        <div className="sidebar">
            {/* premier bloc la photo et le nom */}
            <div className="sidebarId">
                <div className="sidebarIdContent">
                    <img src="./media/idImage+petite.jpg" alt="profil-pic" />
                    <h3>isabelle Guilherme</h3>
                </div>
            </div>
            {/* deuxième bloc la navigation dans les pages du site*/}
            <div className="sidebarNav">
                <ul>
                    <li>
                        <NavLink exact to="/" activeClassName="sidebarNavActive"> 
                            <i className="fas fa-home"></i>
                            <span>Accueil</span>
                        </NavLink>
                    </li>

                    <li>
                        <NavLink to="/Competences" activeClassName="sidebarNavActive"> 
                            <i className="fas fa-mountain"></i>
                            <span>Compétences</span>
                        </NavLink>
                    </li>

                    <li>
                        <NavLink to="/Portfolio" activeClassName="sidebarNavActive"> 
                            <i className="fas fa-images"></i>
                            <span>Portfolio</span>
                        </NavLink>
                    </li>

                    <li>
                        <NavLink to="/Contacts" activeClassName="sidebarNavActive"> 
                            <i className="fas fa-address-book"></i>
                            <span>Contacts</span>
                        </NavLink>
                    </li>
                </ul>

            </div>
            {/* troisième bloc avec l'accès aux réseaux sociaux et le copyright*/}
            <div className="sidebarSocialNetworks">
                <ul>
                    <li>
                        <a href="https://facebook.com/" target="_blank" rel="noopener noreferrer"><i className="fab fa-facebook"></i></a>
                    </li>
                
                    <li>
                        <a href="https://github.com/isa-code" target="_blank" rel="noopener noreferrer"><i className="fab fa-github"></i></a>
                    </li>
                
                    <li>
                        <a href="https://fr.linkedin.com/" target="_blank" rel="noopener noreferrer"><i className="fab fa-linkedin"></i></a>
                    </li>
                
                    <li>
                        <a href="https://twitter.com/" target="_blank" rel="noopener noreferrer"><i className="fab fa-twitter"></i></a>
                    </li>
               
                    <li>
                        <a href="https://codepen.io/isa-code" target="_blank" rel="noopener noreferrer"><i className="fab fa-codepen"></i></a>
                    </li>
                </ul>

                <div className="sidebarSignature">
                    <p>isa-code Copyright 2020</p>
                </div>

            </div>
        </div>
    );
}

export default Sidebar;